@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
/* Use this file to manipulate bootstrap css */
/* Navbar START */

.row {
  width: 420px !important;
  flex-wrap: nowrap !important;
  margin-left: 0px !important;
}

.gradient .links {
  color: #fff;
  position: relative;
  left: 34px;
  width: 100px;
  font-family: var(--primary-font);
}

.gradient .links:hover {
  text-decoration: underline;
  color: #fff;
}

.navbar-dark .navbar-nav .nav-link {
  color: #fff !important;
}

/* dropdown */
.dropdown-menu-dark {
  color: #dee2e6;
  background-color: var(--primary-color) !important;
  border: 2px solid rgba(0, 0, 0, 0.15) !important;
}
.dropdown-menu[data-bs-popper] {
  left: 107px !important;
}

.nav-item {
  display: flex;
  flex-direction: row;
}
.nav-items {
  position: relative;
}
.nav-height {
  height: 70px !important;
}
.navbar-logo {
  width: 90px;
}

.search-bar {
  width: auto !important;
  position: relative;
  left: 20px;
  width: 450px !important;
  padding: 0.375rem 8.75rem !important;
}
.search-select {
  width: auto !important;
  position: relative;
  right: 2px;
  top: 38px;
  z-index: 100;
}

.search-form {
  position: relative;
  margin: auto;
  bottom: 50px;
  width: 50%;
}

.search-button {
  position: relative;
  width: 50px;
  height: 40px;
  left: 450px;
  bottom: 40px;
  z-index: 100;
  background: var(--primary-gradient-color) !important;
}

.signin-btn {
  color: #fff !important;
  border: none !important;
  width: 100px !important;
  margin-right: 13px;
  outline: none !important;
  font-family: var(--primary-font);
}

.signin-btn:hover,
.signin-btn:active,
.signup-btn:hover,
.signup-btn:active {
  background: var(--primary-gradient-color) !important;
}

.signin-btn:focus,
.signup-btn:focus {
  box-shadow: none !important;
}

.toggle-password {
  position: absolute;
  display: flex;
  align-self: flex-end;
  justify-content: flex-end;
  left: 390px !important;
  margin-top: -25px;
  border: none !important;
}

/* Navbar END */

/* Hero START */

.hero .hero-text {
  width: 492px;
}

.hero .submit-btn {
  font-weight: bold;
}

.hero .submit-btn:hover {
  background: var(--primary-gradient-color) !important;
  border: 1px solid var(--primary-gradient-color) !important;
  outline: none;
  color: var(--white-color) !important;
}

.submit-btn:focus {
  box-shadow: none !important;
}

.hero .apply-btn {
  margin-left: 15px;
  font-weight: bold;
}

.hero .apply-btn:hover {
  background: var(--primary-gradient-color) !important;
}

.hero .hero-img {
  position: absolute;
  right: 120px;
  bottom: -10px;
  width: 385px;
}
/* Hero END */

/* Modal START */

.title-modal {
  margin-left: 20%;
}

.title-modal-applied {
  margin-left: 31%;
}

.modal-applied-p {
  margin-left: 7%;
  color: var(--header-color);
  font-weight: 400;
  width: 430px !important;
}

.modal-header-p {
  margin-left: 15%;
  color: var(--header-color);
  font-weight: 500;
}
.form-row {
  width: 180px !important;
  margin-left: 11% !important;
}
.form-group-control {
  width: 335px;
  margin-left: 14%;
}
.form-group-control-last {
  position: relative;
  bottom: 20px;
}
.form-control {
  border: 1px solid var(--primary-color) !important;
}

.button-link {
  position: relative;
  bottom: 3px;
  background-color: #fff !important;
  color: var(--primary-color) !important;
  border-color: #fff !important;
  text-decoration: underline !important;
}
.button-link:active:focus {
  box-shadow: none !important;
}
/* Modal END */

/* Card */

.card-container {
  width: 400px !important;
  height: auto;
  padding: 10px !important;
}
.card {
  border-radius: 7px !important;
  box-shadow: var(--drop-shadow) !important;
}

.card-body {
  padding: 0rem 0rem !important;
  position: relative;
  top: 8px;
}
.card .title-header {
  margin-left: 12px !important;
}

.card {
  height: 420px !important;
  width: 401px;
}

.price-banner {
  background: #227c8e;
  height: 35px;
  color: #fff;
  font-size: 30px;
  box-shadow: 1px 0px px rgba(0, 0, 0, 0.3) !important;
  border-radius: 7px;
}

.card-title {
  margin-bottom: 0 !important;
}
.card-header-css {
  width: 375px;
  font-size: 17px !important;
  background: var(--white-color) !important;
}

/* Footer */

.height-footer {
  height: 400px !important;
  margin-top: 103px;
}

.height-footer .nav-item a {
  color: var(--white-color) !important;
}
.height-footer .nav-item a:hover {
  text-decoration: underline !important;
}
.height-footer .links {
  width: 200px !important;
}

/* Account Settings */
.nav-pills .nav-link.active {
  text-decoration: underline !important;
  color: var(--primary-color) !important;
  background-color: transparent !important;
}
.nav-item h4 {
  cursor: pointer;
}

/*  */

.Toastify__toast-container--top-center {
  top: 5em !important;
  left: 50% !important;
  transform: translateX(-50%) !important;
}
.Toastify__toast-container {
  z-index: 9999 !important;
  --webkit-transform: translate3d(0, 0, 9999px) !important;
  position: fixed !important;
  padding: 4px !important;
  width: 320px !important;
  box-sizing: border-box !important;
  color: #fff !important;
}

@media screen and (max-width: 991.98px) {
  .row {
    flex-wrap: wrap !important;
  }
  .form-control-width {
    width: 335px !important;
  }
  .search-select {
    visibility: hidden;
  }

  .search-form {
    position: relative;
    right: 108px;
    bottom: 20px;
    width: 50%;
  }

  .search-button {
    position: relative;
    left: 340px;
    bottom: 40px;
  }
  .search-bar {
    width: auto !important;
    position: relative;
    left: 20px;
    width: 320px !important;
    padding: 0.375rem 0.3rem !important;
  }
  .button-mobile {
    margin-top: 15px !important;
    /* margin-left: 22% !important; */
  }
  .nav-footer {
    position: relative;
    margin-left: 35px !important;
  }

  /* Hero */

  .hero .hero-text {
    width: 452px;
    /* margin: auto; */
  }

  .hero .hero-img {
    position: relative;
    left: 0;
    top: 25px;
    padding: 15px 0;
    width: 350px;
  }
  .signup-btn {
    position: relative;
    left: 146px !important;
  }
  .signin-btn {
    position: relative;
    bottom: 11px;
    width: 350px !important;
    left: 4px;
  }
  .gradient .links {
    left: 0px;
  }
}

@media screen and (max-width: 890px) {
  /* navbar */
  /* .gradient .links {
    left: 0px;
  }

  .search-select {
    visibility: hidden;
  } */
}

@media (max-width: 768px) {
  .map-container {
    width: 47.2% !important;
  }
}

@media (max-width: 425px) {
  .result-map-container {
    position: absolute !important;
    width: 100% !important;
  }
  .map-container {
    right: 0;
    width: 40% !important;
  }
}

