/* CSS RESET */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  list-style-type: none !important;
  text-decoration: none !important;
}

html,
body {
  background-color: #f6f9fc !important;
  background-color: var(--bg-color) !important;
}

textarea {
  resize: none !important;
  height: 80px !important;
}

:root {
  --primary-gradient-color: linear-gradient(
    90deg,
    rgba(15, 160, 178, 1) 0%,
    rgba(3, 127, 155, 1) 100%
  );
  --primary-font: "Lato", sans-serif;
  --primary-color: #0b97ab;
  --primary-color-hover: #086f80;
  --green-color: #35c03a;
  --red-color: #ed455d;
  --header-color: #5f6368;
  --bg-color: #f6f9fc;
  --drop-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3);
  --border-radius: 5px;
  --white-color: #fff;
  --font-size-18: 18px;
  --font-size-14: 14px;
  --font-size-xsm: 20px;
  --font-size-sm: 25px;
  --font-size-md: 30px;
  --font-size-lg: 45px;
  --border-radius: 5px;
}

/* UniversaL CSS */
.drop-shadow-text {
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3) !important;
  text-shadow: var(--drop-shadow) !important;
}
/* row */
.row-width-margin {
  position: relative;
  width: 337px !important;
  flex-wrap: nowrap !important;
  margin-left: 40px !important;
  margin-bottom: 10px !important;
  top: -5px !important;
}
/* text */
.text-shadow {
  text-shadow: 1px 1px #fff, 1px 1px #ffff, 1px 0px #fff;
}
/* Background */
.gradient {
  background: linear-gradient(
    90deg,
    rgba(15, 160, 178, 1) 0%,
    rgba(3, 127, 155, 1) 100%
  ) !important;
  background: var(--primary-gradient-color) !important;
}

/* Color */
.white-color {
  color: #fff !important;
  color: var(--white-color) !important;
}
.primary-color {
  color: #0b97ab !important;
  color: var(--primary-color) !important;
}

.header-color {
  color: #5f6368;
  color: var(--header-color);
}

.green-color {
  color: #35c03a;
  color: var(--green-color);
}

.red-color {
  color: #ed455d;
  color: var(--red-color);
}

/* Font */
.primary-font {
  font-family: "Lato", sans-serif;
  font-family: var(--primary-font);
}
.font-weight-bold {
  font-weight: bold;
}
.font-weight-200 {
  font-weight: 200 !important;
}

/* Font Size */
.font-size-18 {
  font-size: 18px !important;
  font-size: var(--font-size-18) !important;
}
.font-size-14 {
  font-size: 14px !important;
  font-size: var(--font-size-14) !important;
}
.font-size-xsm {
  font-size: 20px !important;
  font-size: var(--font-size-xsm) !important;
}
.font-size-sm {
  font-size: 25px !important;
  font-size: var(--font-size-sm) !important;
}
.font-size-md {
  font-size: 30px !important;
  font-size: var(--font-size-md) !important;
}

/* Buttons */

.link-btn {
  width: 350px !important;
  height: 32px !important;
  color: #fff !important;
  color: var(--white-color) !important;
  border: 2px solid #0b97ab !important;
  border: 2px solid var(--primary-color) !important;
  border-radius: 5px !important;
  border-radius: var(--border-radius) !important;
  font-family: "Lato", sans-serif;
  font-family: var(--primary-font);
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3) !important;
  box-shadow: var(--drop-shadow) !important;
  font-weight: 400;
  background-color: #0b97ab !important;
  background-color: var(--primary-color) !important;
}

.outline-white {
  color: #fff !important;
  color: var(--white-color) !important;
  border: 2px solid #fff !important;
  border: 2px solid var(--white-color) !important;
  width: 170px !important;
  font-family: "Lato", sans-serif;
  font-family: var(--primary-font);
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3) !important;
  box-shadow: var(--drop-shadow) !important;
}

.primary-color-btn {
  background-color: #0b97ab !important;
  background-color: var(--primary-color) !important;
  border: 2px solid #0b97ab !important;
  border: 2px solid var(--primary-color) !important;
  border-radius: 5px !important;
